import { useState } from 'react';
import './styles.css';
import musics from '../../musics';
import Button from '../button';
import next from '../../assets/next.svg';
import pause from '../../assets/pause.svg';
import play from '../../assets/play.svg';
import previous from '../../assets/previous.svg';
import stop from '../../assets/stop.svg';

function Footer({ id, title, artist, description, url, cover, handleMusic }) {

    let [musicPlaying, setMusicPlaying] = useState(false);
    function handlePlayAndPause() {
        if (musicPlaying) {
            document.querySelector('audio').pause();
            return setMusicPlaying(false);
        }
        document.querySelector('audio').play();
        return setMusicPlaying(true);
    }
    function handleStop() {
        document.querySelector('audio').pause();
        document.querySelector('audio').currentTime = 0;
        return setMusicPlaying(false);
    }
    function handleChangeMusic(value) {
        let index = musics.findIndex((music) => music.id === id) + value;
        if (index >= musics.length) {
            index = 0;
        }
        if (index < 0) {
            index = musics.length - 1;
        }
        const newMusic = {
            id: musics[index].id,
            title: musics[index].title,
            artist: musics[index].artist,
            description: musics[index].description,
            url: musics[index].url,
            cover: musics[index].cover
        }
        handleMusic(newMusic);
        setMusicPlaying(false);
    }
    return (
        <footer>
            <div className='player'>
                <p>{title}</p>
                <span>{artist}</span>
                <audio src={url} type='audio/mp3' />
            </div>
            <div className='controls'>
                <div className='controls__buttons'>
                    <Button
                        className='btn'
                        src={stop}
                        alt='stop'
                        onClick={handleStop}
                    />
                    <Button
                        className='btn'
                        src={previous}
                        alt='previous music'
                        onClick={handleChangeMusic}
                        value={-1}
                    />
                    <Button
                        className='btn-main'
                        src={musicPlaying ? pause : play}
                        alt='play and pause'
                        onClick={handlePlayAndPause}
                    />
                    <Button
                        className='btn'
                        src={next}
                        alt='next music'
                        onClick={handleChangeMusic}
                        value={1}
                    />
                </div>
                <div className='controls__bar'>
                    {/* <span>0:00 ---------------------------------------------------------------------------------------------------------- 3:00</span> */}
                </div>
            </div>
        </footer>
    )
}

export default Footer;