import './styles.css';
import logo from '../../assets/logo.svg';



function Header() {
    return (
        <header>
            <img className='logo' src={logo} alt='logo' />
            <div>
                <img className='avatar' src='https://avatars.githubusercontent.com/u/96790889' alt='user' />
                <span>Welcome, Bruno</span>
            </div>
        </header>
    )
}

export default Header;