import './styles.css';

function Card({ title, artist, description, url, cover, handleMusic, music }) {
    return (
        <div
            className='card'
            onClick={() => handleMusic(music)}
        >
            <img src={cover} alt='album cover' />
            <h4>{title}</h4>
            <p>{description}</p>
        </div>
    )
}

export default Card;